import { Link, useSearchParams } from 'react-router-dom';
import css from './index.module.css';
import { useEffect } from 'react';
import { useActivateEmailMutation } from '../../../redux/apis/authApi';
import { ModalTemplate } from '../ModalTemplate';
import { SpinnerScreen } from '../../../components/SpinnerScreen';
import { AuthNestedPaths, NavPaths } from '../../../navigation';

export function EmailVerification() {
  const [searchParams] = useSearchParams();
  const [activateEmail, { isLoading, isSuccess }] = useActivateEmailMutation();

  const uidb64 = searchParams.get('uidb64');
  const token = searchParams.get('token');

  useEffect(() => {
    if (uidb64 && token) {
      activateEmail({ uidb64, token });
    }
  }, [uidb64, token]);

  return (
    <ModalTemplate
      heading='  '
      width={700}
    >
      {
        isLoading
        ? <SpinnerScreen height={300} />
        : isSuccess
        ? <Success />
        : <Error />
      }
    </ModalTemplate>
  );
}

const Success = () => (
  <p>
    <span className={css.success}>  ! </span>
       <Link to={`${NavPaths.AUTH}/${AuthNestedPaths.LOGIN}`} className='link'></Link>  .
  </p>
);

const Error = () => (
  <div className={css.container}>
    <p><span className={css.error}> !</span>  :</p>

    <ul className={css.list}>
      <li>   -. <Link to={`${NavPaths.AUTH}/${AuthNestedPaths.EMAIL_RESEND}`} className='link'>  </Link>?</li>
      <li>-   .  <Link to={`${NavPaths.AUTH}/${AuthNestedPaths.LOGIN}`} className='link'></Link>  .</li>
    </ul>
  </div>
);
