import { Button } from '../Button';
import { ReactComponent as ShoppingCart } from '../../images/shopping-cart.svg';
import { ReactComponent as Check } from '../../images/check.svg';
import css from './index.module.css';
import { useToggleCart } from '../../redux/apis/productsApi';
import { useAppSelector } from '../../redux/store';
import { toast } from 'react-toastify';

interface Props {
  variantId: number;
  isInCart: boolean;
  amount?: number;
}

export function AddToCartButton({
  variantId,
  isInCart,
  amount=1,
}: Props) {
  const {
    toggleCart,
    isLoading,
  } = useToggleCart({ variantId, isInCart, amount });
  const user = useAppSelector((state) => state.userState.user);

  function onClick(e: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
    e.preventDefault();
    if (user) {
      toggleCart();
    } else {
      toast(',   ', { type: 'error' })
    }
  }

  return (
    <Button
      onClick={onClick}
      isLoading={isLoading}
      isActive={isInCart}
      state={{
        default: {
          text: ' ',
          icon: <ShoppingCart className={css.shoppingCartSVG} />,
        },
        active: {
          text: '',
          icon: <Check className={css.checkSVG} />,
        },
      }}
    />
  );
}
